.loadingPage {
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
    background-color: transparent;
}

.loadingCircle {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 10px solid white;
    border-top: 10px solid rgb(10, 84, 107);
    border-bottom: 10px solid rgb(10, 84, 107);
    animation: loading 1s infinite
}

@keyframes loading {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}